<template>
  <v-dialog v-model="isOpen" max-width="700px" persistent>
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="deviceName" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.point_name" label="Point name" required :rules="[() =>!onCheckExisting(editedItem, 'point_name') || 'Name Existed', rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.point_enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.point_object_id" label="point object id" type="number" @input="numberChange('point_object_id')"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select v-model="editedItem.point_object_type" :items="point_object_types" label="point object type" required :rules="[rules.required]"></v-select>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="()=>{onClose(); $refs.form.resetValidation()}">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="submit" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
import { RULES, POINT_OBJECT_TYPES } from './bacnetMasterConstant';

export default {
  props: ['on-close', 'is-open', 'on-save', 'form-title', 'device-name', 'edited-item', 'on-check-existing'],
  data: () => ({
    rules: {...RULES},
    valid: false,
    point_object_types: [...POINT_OBJECT_TYPES],
  }),
  methods:{
    submit() {
      if (this.$refs.form.validate()) {
        this.onSave();
      }
    },
    numberChange(fieldName){
      this.editedItem[fieldName] = this.editedItem[fieldName] ? Number(this.editedItem[fieldName]) : 0;
    },
  }
};
</script>


