<template>
  <v-app>
    <bm-right-sidebar></bm-right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right dark>
          <v-breadcrumbs :items="dirs" class="breadcrumbs">
            <template v-slot:item="{ item }">
              <v-breadcrumbs-item class="breadcrumbs-item-text" :to="item.link" exact> {{ item.text }} </v-breadcrumbs-item>
            </template>
          </v-breadcrumbs>
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="pointHeaders"
              :items="dataTable"
              class="elevation-1 text-left"
              item-key="point_uuid"
              show-select
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
              sort-by="point_uuid"
              group-by="category"
              show-group-by
              dense
              :loading="loadingData"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Points View</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <v-btn @click="onClickRefresh" x-small color="primary" class="white--text"> Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn >&nbsp;
                    <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text" >Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn>&nbsp;
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                </v-toolbar>
              </template>

              <template v-slot:group.header="{ group, headers, toggle, isOpen, items }">
                <td :colspan="headers.length">
                  <div class="row" style="align-items: center;">
                    <v-btn @click="toggle" x-small icon :ref="group">
                        <v-icon>{{ isOpen ? 'mdi-chevron-double-down' : 'mdi-chevron-double-right' }}</v-icon>
                    </v-btn>&nbsp;&nbsp;&nbsp;&nbsp;
                    <v-checkbox v-bind:value="selected.filter(point=>point.device_uuid === items[0].device_uuid).length == items.length" @change="check($event, items)"></v-checkbox>
                    <router-link :to="`/bacnet-master/devices/${items[0].device_uuid}/points`" class="font-weight-bold text-style"> {{group}} </router-link>
                  </div>
                </td>
              </template>

              <template v-slot:item.point_enable="{ item }">
                <v-checkbox v-model="item.point_enable" @click="updatePoint(item)"></v-checkbox>
              </template>

              <template v-slot:item.priority_array="{ item }" >
                <div class="overflow-td">
                  <span v-for="(key, index) of item.priority_array ? Object.keys(item.priority_array): []" :key="index" >
                    {{key}}:&nbsp;<span class="value">{{ item.priority_array[key] || item.priority_array[key] == 0 ? item.priority_array[key] : 'null' }}</span>,&nbsp;
                  </span>
                </div>
              </template>

              <template v-slot:item.actions="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)" color="primary"> mdi-pencil </v-icon>
                <v-icon small @click="clickDelete(item)" color="error"> mdi-delete </v-icon>
              </template>

              <!-- header -->
              <template v-slot:header.point_enable="{ item }">
                {{ toUpperTextKey('enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('point_enable'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>

              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="1">
            <system-status-table server='bacnet-master'></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!--Edit dialog-->
    <bm-point-form-dialog 
      :is-open="dialog" 
      :on-close="close" 
      :on-save="()=>updatePoint(editedItem)"
      :form-title="'Edit Device'"
      :device-name="editedItem && editedItem.category ? editedItem.category.split(':')[1] : ''"
      :edited-item="editedItem"
      :on-check-existing="isExistingvalue"
    ></bm-point-form-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <bm-mass-edit-dialog 
      :is-open="dialogMassEdit" 
      :on-close="()=>dialogMassEdit = false" 
      :edit-all="editAll"
      :edit-field="editField"
      :on-save="saveMassEdit"
    ></bm-mass-edit-dialog>

  </v-app>
</template>

<script>
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import BmRightSidebar from './BmRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import BmMassEditDialog from './BmMassEditDialog';
import BmPointFormDialog from './BmPointFormDialog';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { POINT_HEADERS, POINT_OBJECT_TYPES, RULES } from './bacnetMasterConstant';
import { toUpperTextKey } from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    BmRightSidebar,
    ConfirmDialog,
    BmMassEditDialog,
    BmPointFormDialog,
    SystemStatusTable,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    action: '',
    network: {},
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    pointHeaders: [...POINT_HEADERS],
    point_object_types: [...POINT_OBJECT_TYPES],
    currentItem: {},
    editedItem: { },
    rules: {...RULES},
    search: '',
    dialogMassEdit: false,
    editAll: {},
    editField: "",
    intervalId: null,
    pointsOfDevice: [],
    dataTable: [],
    dirs: [],
    tableProps: {...DATA_TABLE_PROPS},
    tab: 0,
    loadingData: false,
  }),
  computed: {
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getAllPoints();
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getAllPoints();

    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getAllPoints();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      this.loadingData = true;
      axios.get(`bacnet_master/network/${this.$route.params.id}`).then(data => {
        this.network = data;
        this.getDataTable(data.devices);
        //get dirs
        if (this.dirs.length != 2) {
          this.getDirs();
        }
        this.loadingData = false;
      });
    },
    getDataTable(devices){
      this.dataTable = [];
      devices.forEach(device=>{
        if(device.points.length > 0){
          const pointsPerDevice = device.points.map(point=>{
            point = { ...point, category: device.device_name };
            return point;
          });
          this.dataTable = this.dataTable.concat(pointsPerDevice);
        }
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable.findIndex(p=>p.point_uuid == item.point_uuid);
      this.pointsOfDevice = this.dataTable.filter(point=>point.device_uuid === item.device_uuid);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable.indexOf(this.currentItem);
      this.dataTable.splice(this.editedIndex, 1);
      await axios.delete(`bacnet_master/point/${this.currentItem.point_uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(item, fieldName) {
      if(!this.pointsOfDevice) return false;
      const value = item[fieldName];
      let points = _cloneDeep(this.pointsOfDevice);
      const index = points.findIndex(p=>p.point_uuid == item.point_uuid);
      points.splice(index, 1);
      return points.find(p => p[fieldName] == value) != null;
    },
    updatePoint(data) {
      const _data = {
        point_name:data.point_name,
        point_enable:data.point_enable,
        point_object_id:data.point_object_id,
        point_object_type:data.point_object_type,
        device_uuid:data.device_uuid
      }
      axios.patch(`bacnet_master/point/${data.point_uuid}`, _data)
        .then(response => {
          const index = this.dataTable.findIndex(n => n.point_uuid == response.point_uuid);
          Object.assign(this.dataTable[index], response);
          this.$toasted.show('update success');
          this.close();
        });
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editAll = { point_enable: false, }
      this.editField = fieldName;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.dataTable.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    getDirs(){
      this.dirs = [{text: "NETWORKS", link: '/bacnet-master/networks'}];
      this.dirs.push({ text: `NET: ${this.network.network_name}`, disabled: true });
    },
    onClickRefresh(){
      this.getAllPoints();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if(!e) this.selected = [...this.selected].filter(point=>point.device_uuid != items[0].device_uuid);
      else this.selected = [...this.selected].concat(items);
    },
  },
};
</script>
<style scoped>
.centered-input >>> input {
  text-align: center;
}
.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}
.breadcrumbs{
  margin-left: 0 !important;
  color: white;
  width: 800px;
}
.breadcrumbs-item-text {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.table-styling {
  margin-right: 5px;
  margin-left: -5px;
}
.set-row-height {
  height: 85px !important;
}
.overflow-td {
  overflow-y: auto;
  width: 150px;
  height: 40px;
  word-break: break-word;
}
.value {
  font-weight: bold;
  opacity: 0.6;
}
</style>
